*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  font-family: sans-serif;
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

article, aside, figcaption, figure, footer, header, hgroup, main, nav, section {
  display: block;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #212529;
  text-align: left;
  background-color: #fff;
}

[tabindex="-1"]:focus {
  outline: 0 !important;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
  margin-bottom: 0.5rem;
}

p {
  margin-top: 0;
  margin-bottom: 1rem;
}

abbr[title],
abbr[data-original-title] {
  text-decoration: underline;
  text-decoration: underline dotted;
  cursor: help;
  border-bottom: 0;
  text-decoration-skip-ink: none;
}

address {
  margin-bottom: 1rem;
  font-style: normal;
  line-height: inherit;
}

ol,
ul,
dl {
  margin-top: 0;
  margin-bottom: 1rem;
}

ol ol,
ul ul,
ol ul,
ul ol {
  margin-bottom: 0;
}

dt {
  font-weight: 700;
}

dd {
  margin-bottom: 0.5rem;
  margin-left: 0;
}

blockquote {
  margin: 0 0 1rem;
}

b,
strong {
  font-weight: bolder;
}

small {
  font-size: 80%;
}

sub,
sup {
  position: relative;
  font-size: 75%;
  line-height: 0;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

a {
  color: #007bff;
  text-decoration: none;
  background-color: transparent;
}
a:hover {
  color: #0056b3;
  text-decoration: underline;
}

a:not([href]):not([tabindex]) {
  color: inherit;
  text-decoration: none;
}
a:not([href]):not([tabindex]):hover, a:not([href]):not([tabindex]):focus {
  color: inherit;
  text-decoration: none;
}
a:not([href]):not([tabindex]):focus {
  outline: 0;
}

pre,
code,
kbd,
samp {
  font-family: SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
  font-size: 1em;
}

pre {
  margin-top: 0;
  margin-bottom: 1rem;
  overflow: auto;
}

figure {
  margin: 0 0 1rem;
}

img {
  vertical-align: middle;
  border-style: none;
}

svg {
  overflow: hidden;
  vertical-align: middle;
}

table {
  border-collapse: collapse;
}

caption {
  padding-top: 0.75rem;
  padding-bottom: 0.75rem;
  color: #6c757d;
  text-align: left;
  caption-side: bottom;
}

th {
  text-align: inherit;
}

label {
  display: inline-block;
  margin-bottom: 0.5rem;
}

button {
  border-radius: 0;
}

button:focus {
  outline: 1px dotted;
  outline: 5px auto -webkit-focus-ring-color;
}

input,
button,
select,
optgroup,
textarea {
  margin: 0;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

select {
  word-wrap: normal;
}

button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  padding: 0;
  border-style: none;
}

input[type=radio],
input[type=checkbox] {
  box-sizing: border-box;
  padding: 0;
}

input[type=date],
input[type=time],
input[type=datetime-local],
input[type=month] {
  -webkit-appearance: listbox;
}

textarea {
  overflow: auto;
  resize: vertical;
}

fieldset {
  min-width: 0;
  padding: 0;
  margin: 0;
  border: 0;
}

legend {
  display: block;
  width: 100%;
  max-width: 100%;
  padding: 0;
  margin-bottom: 0.5rem;
  font-size: 1.5rem;
  line-height: inherit;
  color: inherit;
  white-space: normal;
}

progress {
  vertical-align: baseline;
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

[type=search] {
  outline-offset: -2px;
  -webkit-appearance: none;
}

[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  font: inherit;
  -webkit-appearance: button;
}

output {
  display: inline-block;
}

summary {
  display: list-item;
  cursor: pointer;
}

template {
  display: none;
}

[hidden] {
  display: none !important;
}

html {
  font-size: 62.5%;
}

body {
  background: #fff;
  color: #000;
  font: 400 1.4rem/1.4 Verdana, Geneva, sans-serif;
  display: flex;
  width: 100%;
  height: 100vh;
  align-items: stretch;
}
@media (prefers-color-scheme: dark) {
  body {
    background: #020305;
    color: #fff;
  }
}

p, span, div, label {
  color: inherit;
}

a {
  color: #1877DD;
  transition: opacity 0.25s ease;
}
a:hover {
  color: #1568c1;
  text-decoration: none;
  opacity: 0.7;
}
a:visited {
  color: #1877DD;
}

h1, #h1 {
  background: none;
  border: none;
  color: #000;
  font-size: 2.8rem;
  font-style: normal;
  padding: 0;
  margin: 0;
}
@media (prefers-color-scheme: dark) {
  h1, #h1 {
    color: #fff;
  }
}

#h1:before {
  background: url(data:image/svg+xml;base64,) no-repeat center center/100%;
  content: "";
  display: inline-block;
  vertical-align: baseline;
  width: 6rem;
  height: 5rem;
}
@media (prefers-color-scheme: dark) {
  #h1:before {
    background-image: url(data:image/svg+xml;base64,);
  }
}

h2 {
  background: none;
  border: none;
  font-size: 3.2rem;
  margin: 0 0 1em;
  padding: 0;
  color: #000;
  word-spacing: 80vw;
  word-wrap: break-word;
}
h2:first-line {
  font-size: 4.8rem;
}
@media (prefers-color-scheme: dark) {
  h2 {
    color: #fff;
  }
}

#menu {
  background: whitesmoke;
  display: block;
  flex: 0 0 32rem;
  margin: 0;
  padding: 4rem;
  position: relative;
  top: auto;
  left: auto;
  width: auto;
  order: -1;
  overflow-x: auto;
  scrollbar-width: thin;
  scrollbar-color: whitesmoke whitesmoke;
}
#menu::-webkit-scrollbar {
  width: 10px;
}
#menu::-webkit-scrollbar-track {
  background: whitesmoke;
}
#menu:hover {
  scrollbar-color: #1877DD #fff;
}
#menu:hover::-webkit-scrollbar-thumb {
  background: #1877DD;
}
@media (prefers-color-scheme: dark) {
  #menu {
    background: #020305;
    color: #fff;
    scrollbar-width: thin;
    scrollbar-color: #020305 #020305;
  }
  #menu::-webkit-scrollbar {
    width: 10px;
  }
  #menu::-webkit-scrollbar-track {
    background: #020305;
  }
  #menu:hover {
    scrollbar-color: #1877DD #020305;
  }
  #menu:hover::-webkit-scrollbar-thumb {
    background: #1877DD;
  }
}
#menu select {
  background: #fff;
}
@media (prefers-color-scheme: dark) {
  #menu select {
    background: #080c14;
  }
}
#menu p {
  border: none;
  padding: 0;
  margin: 2rem 0;
}
#menu .version {
  color: #1877DD;
  font-size: 1.6rem;
}
#menu .links {
  border: none;
  padding: 0;
}
#content {
  flex: 0 0 calc(100% - 32rem);
  margin: 0;
  padding: 4rem;
  overflow: auto;
  scrollbar-width: thin;
  scrollbar-color: #fff #fff;
}
#content::-webkit-scrollbar {
  width: 10px;
}
#content::-webkit-scrollbar-track {
  background: #fff;
}
#content:hover {
  scrollbar-color: #1877DD #fff;
}
#content:hover::-webkit-scrollbar-thumb {
  background: #1877DD;
}
@media (prefers-color-scheme: dark) {
  #content {
    background: #080c14;
    scrollbar-width: thin;
    scrollbar-color: #080c14 #080c14;
  }
  #content::-webkit-scrollbar {
    width: 10px;
  }
  #content::-webkit-scrollbar-track {
    background: #080c14;
  }
  #content:hover {
    scrollbar-color: #1877DD #080c14;
  }
  #content:hover::-webkit-scrollbar-thumb {
    background: #1877DD;
  }
}
#content p {
  margin-right: 0;
}
#content .links {
  display: flex;
  margin: 0 0 2rem 0;
}
#content .links a {
  border-bottom: 0.3rem solid transparent;
  font-size: 1.6rem;
  line-height: 1.4;
  padding: 0.6em 1.4em;
  margin: 0;
}
#content .links a:hover {
  border-color: #1877DD;
  color: inherit;
  text-decoration: none;
}
#content .links a.active {
  border-color: #1877DD;
  font-weight: 400;
}
#content #form fieldset {
  margin-right: 2rem;
}
#content #form input[type=submit] {
  font-size: 80%;
}
#content #form select, #content #form input:not([type=image]):not([type=checkbox]):not([type=radio]) {
  min-width: 6rem;
  margin-right: 1rem;
}
#content #form select:last-of-type, #content #form input:not([type=image]):not([type=checkbox]):not([type=radio]):last-of-type {
  margin-right: 0;
}
#content #form #fieldset-search select {
  margin-right: 1rem;
}
#content #form #fieldset-sort label {
  margin-left: 1rem;
}
#content #form #fieldset-sort label input {
  margin-right: 0.6em;
}
#content form fieldset > div {
  margin: 2rem 0;
}
#content form fieldset > div input[type=search][name=query] {
  font-size: 120%;
  height: 4.4rem;
  margin-right: 1em;
}

table {
  border: 0.1rem solid #e6e6e6;
  font-size: 1.2rem;
  margin: 0 0 2.4rem;
}
@media (prefers-color-scheme: dark) {
  table {
    border-color: #020305;
  }
}
table code {
  padding: 0.4em;
}

th, td {
  background: none;
  border: none;
  padding: 0.5rem 1.6rem;
  height: 3.6rem;
  vertical-align: middle;
}

thead th, thead td {
  background: whitesmoke;
  font-size: 1.4rem;
  font-weight: 500;
}
@media (prefers-color-scheme: dark) {
  thead th, thead td {
    background: #020305;
  }
}

tbody tr {
  border-bottom: 0.1rem solid whitesmoke;
}
@media (prefers-color-scheme: dark) {
  tbody tr {
    border-color: #020305;
  }
}
tbody tr:hover th, tbody tr:hover td {
  background: #fafafa;
}
@media (prefers-color-scheme: dark) {
  tbody tr:hover th, tbody tr:hover td {
    background: #0e1422;
  }
}
tbody .odd td {
  background: none;
}

.js .checkable .checked td, .js .checkable .checked th {
  background: #e6e6e6;
}
@media (prefers-color-scheme: dark) {
  .js .checkable .checked td, .js .checkable .checked th {
    background: #111929;
  }
}

label {
  margin: 0 0.8em 0 0;
}
label input[type=checkbox], label input[type=radio] {
  margin-right: 0.6em;
}

input:not([type=submit]):not([type=checkbox]):not([type=radio]):not([type=image]), select, textarea {
  background: #f7f7f7;
  border: none;
  border-radius: 0.2rem;
  color: #000;
  font-size: 1.4rem;
  padding: 0.2em 1em;
  height: 2.8rem;
}
input:not([type=submit]):not([type=checkbox]):not([type=radio]):not([type=image]):hover, select:hover, textarea:hover {
  background: whitesmoke;
}
input:not([type=submit]):not([type=checkbox]):not([type=radio]):not([type=image]):focus, select:focus, textarea:focus {
  background: #f0f0f0;
  box-shadow: 0 0.2rem 0.5rem 0 rgba(0, 0, 0, 0.26);
  outline: none;
}
@media (prefers-color-scheme: dark) {
  input:not([type=submit]):not([type=checkbox]):not([type=radio]):not([type=image]), select, textarea {
    background: #020305;
    color: #fff;
  }
  input:not([type=submit]):not([type=checkbox]):not([type=radio]):not([type=image]):hover, select:hover, textarea:hover {
    background: #030509;
  }
  input:not([type=submit]):not([type=checkbox]):not([type=radio]):not([type=image]):focus, select:focus, textarea:focus {
    background: #05070c;
  }
}

input[type=checkbox] {
  display: inline-block;
  vertical-align: baseline;
  margin-right: 0.4em;
}

button, input[type=submit], .loadmore {
  background: #1877DD;
  border: none;
  border-radius: 0.6rem;
  box-shadow: none;
  color: #fff;
  cursor: pointer;
  display: inline-block;
  font-size: 1.6rem;
  padding: 0.6em 2em;
  margin-right: 0.8em;
  outline: none;
}
button:hover, button:visited:hover, button:link:hover, input[type=submit]:hover, input[type=submit]:visited:hover, input[type=submit]:link:hover, .loadmore:hover, .loadmore:visited:hover, .loadmore:link:hover {
  background: #166dcb;
  box-shadow: 0 0.2rem 0.5rem 0 rgba(0, 0, 0, 0.26);
  color: #fff;
  text-decoration: none;
}

.loadmore {
  margin: 0 auto 2rem;
}

code {
  background: whitesmoke;
  border-radius: 0.4rem;
  padding: 2rem;
}
@media (prefers-color-scheme: dark) {
  code {
    background: #020305;
    color: #fff;
  }
}

code.jush-sql {
  display: block;
  width: 100%;
  margin: 2rem 0 0;
}
code.jush-sql + .time {
  display: inline-block;
  margin: 0.6rem 0 2rem auto;
  float: right;
  vertical-align: baseline;
}
code.jush-sql + .time + a {
  display: inline-block;
  margin: 0.6rem 0 2rem;
  vertical-align: baseline;
}
code.jush-sql a {
  color: #1877DD;
}

.jush {
  color: #000;
}
@media (prefers-color-scheme: dark) {
  .jush {
    color: #c9c9c9;
  }
}
.jush a {
  color: #1877DD;
}

#breadcrumb {
  background: none;
  position: static;
  margin: 0 0 2rem 0;
  padding: 0;
}

#logins a, #tables a, #tables span {
  background: none;
}

.footer {
  background: whitesmoke;
  border-top: 0.2rem solid #1877DD;
  border-image: none;
  margin: 0;
  padding: 2rem;
  position: relative;
}
@media (prefers-color-scheme: dark) {
  .footer {
    background: #020305;
    color: #fff;
  }
}
.footer > div {
  background: none;
  padding: 0;
}
.footer input[type=submit] {
  font-size: 80%;
}
.footer p {
  margin-top: 1em;
}
.footer select {
  background: #fff;
}
@media (prefers-color-scheme: dark) {
  .footer select {
    background: #080c14;
  }
}

.logout input[type=submit] {
  font-size: 80%;
}

#tables {
  border: none;
  padding: 0;
}
#tables li {
  display: flex;
}
#tables .select {
  flex: 0 0 5.2rem;
  order: 2;
  margin: 0 0 0 auto;
  text-align: right;
}
#tables .structure {
  flex: 0 1 calc(100% - 5.2rem);
  text-overflow: ellipsis;
  overflow: hidden;
}
#tables .active {
  color: #000;
}
@media (prefers-color-scheme: dark) {
  #tables .active {
    color: #fff;
  }
}